.Notifications {
  border: 1px solid #ddd;
  padding: 12px;
  margin: 10px;
  background: #fff;
  position: relative;
}

.Notifications ul {
  list-style: none;
  padding: 0;
}

.Notifications li[data-priority="default"] {
  color: blue;
}

.Notifications li[data-priority="urgent"] {
  color: red;
}
